@import '/src/theme/_base';

.container {
  &__maskDetectionComponent {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  }

  &__header {
    padding: 10px 0;
    font-size: 18px;
    font-weight: 700;
    font-style: normal;
    line-height: 24px;
    text-align: center;
    color: #000;
    background: white;
  }

  &__flexBox {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  &__rotatedMessage {
    position: absolute;
    bottom: 25px;
    left: 10%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    padding: 2px 8px;
    font-size: 16px;
    background-color: #ffd2d2;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
  }

  &__videoContainer {
    display: flex;
    justify-content: center;
  }

  &__videoElement {
    position: relative;
    width: 53%;
    height: 100%;
  }

  &__suggestionMessage {
    position: absolute;
    bottom: 10%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    padding: 2px 8px;
    font-size: 16px;
    background-color: white;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
  }

  &__buttonAlign {
    position: absolute;
    right: 5%;
    bottom: 3%;
  }

  &__title {
    margin: 46px 0;
    font-size: 24px;
    font-weight: 700;
    font-style: normal;
    line-height: 30px;
    letter-spacing: -0.48px;
    text-align: center;
    color: #000;
  }

  &__mask {
    position: absolute;
    top: 39%;
    left: 37%;
  }
}
