@import '/src/theme/_base';

.container {
  @media (max-width: 768px) {
    &__flexboxItem {
      width: 47%;
    }
  }

  &__padding {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    padding: 24px;
  }

  &__flexbox {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 40px;
  }

  &__flexboxItem {
    justify-content: space-between;
    width: 22%;
    margin-bottom: 24px;
  }

  &__header {
    font-size: 24px;
    font-weight: 700;
    font-style: normal;
    line-height: 30px;
    letter-spacing: -0.48px;
    text-align: center;
    color: #000;
  }

  &__description {
    margin-top: 24px;
    font-size: 16px;
    font-weight: 500;
    font-style: normal;
    line-height: 24px;
    text-align: center;
    color: #000;
  }
}
